export class AudioManager {
    private audioContext: AudioContext | null = null;
    private readonly noteFrequencies: Record<string, number> = {
        'S': 261.63, // C4
        'D': 293.66, // D4
        'F': 329.63, // E4
        'G': 349.23, // F4
        'H': 392.00, // G4
        'J': 440.00, // A4
        'K': 493.88  // B4
    };

    private static readonly AUDIO_CONSTANTS = {
        NOTE_DURATION: 0.5,
        ATTACK_TIME: 0.01,
        VOLUME: 0.3
    } as const;

    async initialize(): Promise<void> {
        try {
            this.audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
            
            // Resume context if suspended (required by some browsers)
            if (this.audioContext.state === 'suspended') {
                await this.audioContext.resume();
            }
        } catch (error) {
            console.warn('Audio initialization failed:', error);
        }
    }

    playNote(key: string): void {
        const frequency = this.noteFrequencies[key];
        if (!frequency || !this.audioContext) {
            console.warn(`Cannot play note: ${key}`);
            return;
        }

        try {
            this.playTone(frequency);
        } catch (error) {
            console.warn('Failed to play note:', error);
        }
    }

    private playTone(frequency: number): void {
        if (!this.audioContext) return;

        const oscillator = this.audioContext.createOscillator();
        const gainNode = this.audioContext.createGain();
        
        oscillator.connect(gainNode);
        gainNode.connect(this.audioContext.destination);
        
        // Configure oscillator
        oscillator.frequency.setValueAtTime(frequency, this.audioContext.currentTime);
        oscillator.type = 'sine';
        
        // Configure volume envelope
        const { VOLUME, ATTACK_TIME, NOTE_DURATION } = AudioManager.AUDIO_CONSTANTS;
        gainNode.gain.setValueAtTime(0, this.audioContext.currentTime);
        gainNode.gain.linearRampToValueAtTime(
            VOLUME, 
            this.audioContext.currentTime + ATTACK_TIME
        );
        gainNode.gain.exponentialRampToValueAtTime(
            0.01, 
            this.audioContext.currentTime + NOTE_DURATION
        );
        
        // Play the note
        oscillator.start(this.audioContext.currentTime);
        oscillator.stop(this.audioContext.currentTime + NOTE_DURATION);
    }

    dispose(): void {
        if (this.audioContext) {
            this.audioContext.close();
            this.audioContext = null;
        }
    }
}