import { Scene } from 'phaser';
import { AudioManager } from '../audio/AudioManager';
import { InputManager } from '../input/InputManager';
import { PlatformManager } from '../platform/PlatformManager';

// Type aliases for better readability
type GameKey = 'S' | 'D' | 'F' | 'G' | 'H' | 'J' | 'K';

// Game configuration interface for better organization
interface GameConfig {
    readonly VALID_KEYS: readonly GameKey[];
    readonly NOTE_FREQUENCIES: Record<GameKey, number>;
    readonly TIMING_WINDOWS: {
        readonly PERFECT: number;
        readonly GOOD: number;
        readonly OKAY: number;
    };
}

export class MuseRunnerGame extends Scene {
    // Game constants
    private static readonly GAME_CONSTANTS = {
        CHARACTER_SPEED: 100,
        JUMP_BASE_VELOCITY: -200,
        JUMP_LEVEL_MULTIPLIER: 50,
        MAX_JUMP_LEVELS: 2,
        PLATFORM_WIDTH: 120,
        PLATFORM_HEIGHT: 20,
        PLATFORM_EXTENSION: 150,
        PLATFORM_AHEAD_OFFSET: 20,
        PLATFORM_CENTER_OFFSET: 80,
        CLEANUP_DISTANCE: 400,
        GAME_OVER_FALL_DISTANCE: 300,
        AUDIO_NOTE_DURATION: 0.5,
        AUDIO_ATTACK_TIME: 0.01,
        AUDIO_VOLUME: 0.3,
        VISUAL_FEEDBACK_DURATION: 200,
        SIMPLE_JUMP_BASE: -300,
        SIMPLE_JUMP_MULTIPLIER: 40,
        LEVEL_HEIGHTS: {
            'S': -80,  // Ground level offset
            'D': -120, // Level 2 offset
            'F': -160, // Level 3 offset
            'G': -200, // Level 4 offset
            'H': -240, // Level 5 offset
            'J': -280, // Level 6 offset
            'K': -320  // Highest level offset
        },
        LEVEL_COLORS: {
            'S': 0xe74c3c, // Red (lowest)
            'D': 0xf39c12, // Orange
            'F': 0xf1c40f, // Yellow
            'G': 0x2ecc71, // Green
            'H': 0x3498db, // Blue
            'J': 0x9b59b6, // Purple
            'K': 0xe91e63  // Pink (highest)
        }
    } as const;

    private character!: Phaser.GameObjects.Rectangle;
    private scoreText!: Phaser.GameObjects.Text;
    
    // Game state
    private isGameRunning: boolean = false;
    private score: number = 0;

    // Managers
    private audioManager!: AudioManager;
    private inputManager!: InputManager;
    private platformManager!: PlatformManager;

    constructor() {
        super('MuseRunnerGame');
    }

    async create(): Promise<void> {
        const { width, height } = this.cameras.main;

        // Initialize managers
        this.audioManager = new AudioManager();
        this.inputManager = new InputManager(this);
        this.platformManager = new PlatformManager(this);

        await this.audioManager.initialize();
        this.inputManager.initialize();
        this.platformManager.initialize();

        // Create character (blue rectangle) - positioned on the neutral platform
        this.character = this.add.rectangle(100, height - 80, 30, 40, 0x3498db);
        this.physics.add.existing(this.character);
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        
        // Set up physics for endless running
        characterBody.setCollideWorldBounds(false); // Don't stop at world bounds
        characterBody.setBounce(0.1);
        characterBody.setDragX(0); // No horizontal drag
        characterBody.setFrictionX(0); // No friction
        characterBody.setMaxVelocityX(200); // Allow fast horizontal movement
        characterBody.setMaxVelocityY(1000); // Allow falling

        // Set up collisions between character and platforms - only from above
        this.physics.add.collider(this.character, this.platformManager.getPlatformsGroup(), undefined, (character, platform) => {
            // Only collide if character is falling down onto platform (not hitting from side/bottom)
            const characterBody = (character as Phaser.GameObjects.Rectangle).body as Phaser.Physics.Arcade.Body;
            const platformBody = (platform as Phaser.GameObjects.Rectangle).body as Phaser.Physics.Arcade.StaticBody;
            
            // Check if character is above the platform and falling down
            return characterBody.velocity.y >= 0 && characterBody.bottom <= platformBody.top + 10;
        });

        // Create UI
        this.scoreText = this.add.text(16, 16, 'Score: 0', {
            fontSize: '24px',
            color: '#ffffff'
        });



        // Instructions
        const instructionText = this.add.text(width / 2, 30, '🎵 Press S-D-F-G-H-J-K to create platforms at different heights! 🎵', {
            fontSize: '16px',
            color: '#ecf0f1',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Key guide
        this.add.text(width / 2, 60, 'S=Low → D → F → G → H → J → K=High', {
            fontSize: '14px',
            color: '#95a5a6',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Make instructions pulse to draw attention
        this.tweens.add({
            targets: instructionText,
            alpha: 0.6,
            duration: 1500,
            yoyo: true,
            repeat: -1
        });

        // Controls info
        this.add.text(width - 16, 16, 'v0.0.4 | ESC: Menu | R: Restart', {
            fontSize: '14px',
            color: '#95a5a6'
        }).setOrigin(1, 0);

        // ESC key to return to menu
        this.input.keyboard!.on('keydown-ESC', () => {
            this.scene.start('MainMenu');
        });

        // R key to restart
        this.input.keyboard!.on('keydown-R', () => {
            this.scene.restart();
        });

        // Start the game
        this.isGameRunning = true;
        
        // Camera follows character
        this.cameras.main.startFollow(this.character, true, 0.1, 0.1);
        this.cameras.main.setDeadzone(200, 100);

        // Reset game state
        this.score = 0;

        // Don't start with any musical platform - wait for first key press
    }

    override update(): void {
        if (!this.isGameRunning) return;

        // Auto-run character forward - ALWAYS moving, never stops
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        
        // Force constant forward movement - override any physics that might stop it
        characterBody.setVelocityX(MuseRunnerGame.GAME_CONSTANTS.CHARACTER_SPEED);
        characterBody.setDragX(0); // No drag to slow down
        characterBody.setFrictionX(0); // No friction from platforms

        // Handle input
        const { keyStateChanged, targetKey } = this.inputManager.update(this.time.now);
        
        // Handle key releases
        const inputState = this.inputManager.getInputState();
        if (keyStateChanged && inputState.pressedKeys.size === 0 && this.platformManager.getCurrentKey()) {
            this.platformManager.endCurrentPlatform();
        }

        // Switch to new key if pressed
        if (keyStateChanged && targetKey && targetKey !== this.platformManager.getCurrentKey()) {
            this.switchToKey(targetKey);
        }

        // Extend current platform as character moves
        if (this.platformManager.hasCurrentPlatform()) {
            this.platformManager.extendCurrentPlatform(this.character.x);
        }

        // Character speed increases over time (for future use)
        // const dynamicSpeed = Math.min(200, 120 + (this.score / 100));

        // Game over condition - only if character falls WAY off screen
        if (this.character.y > this.cameras.main.height + MuseRunnerGame.GAME_CONSTANTS.GAME_OVER_FALL_DISTANCE) {
            this.gameOver();
        }

        // Clean up old platforms
        this.platforms.children.entries.forEach((platform) => {
            const rect = platform as Phaser.GameObjects.Rectangle;
            if (rect.x < this.character.x - MuseRunnerGame.GAME_CONSTANTS.CLEANUP_DISTANCE) {
                rect.destroy();
            }
        });
    }

    private playNote(key: GameKey): void {
        const frequency = this.noteFrequencies[key];
        if (!frequency) {
            console.warn(`Unknown key: ${key}`);
            return;
        }
        
        console.log(`Playing note ${key} at ${frequency}Hz`);
        
        // Play musical note sound
        this.playMusicalTone(frequency);
        
        // Visual feedback - change fill color
        this.character.setFillStyle(0xf39c12);
        this.time.delayedCall(MuseRunnerGame.GAME_CONSTANTS.VISUAL_FEEDBACK_DURATION, () => {
            this.character.setFillStyle(0x3498db); // Back to blue
        });
    }

    private playMusicalTone(frequency: number): void {
        try {
            // Initialize audio context if needed
            if (!this.audioContext) {
                this.audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
            }

            // Create oscillator for the musical note
            const oscillator = this.audioContext.createOscillator();
            const gainNode = this.audioContext.createGain();
            
            oscillator.connect(gainNode);
            gainNode.connect(this.audioContext.destination);
            
            // Set frequency and type
            oscillator.frequency.setValueAtTime(frequency, this.audioContext.currentTime);
            oscillator.type = 'sine'; // Pure tone
            
            // Set volume envelope (attack and decay)
            gainNode.gain.setValueAtTime(0, this.audioContext.currentTime);
            gainNode.gain.linearRampToValueAtTime(
                MuseRunnerGame.GAME_CONSTANTS.AUDIO_VOLUME, 
                this.audioContext.currentTime + MuseRunnerGame.GAME_CONSTANTS.AUDIO_ATTACK_TIME
            );
            gainNode.gain.exponentialRampToValueAtTime(
                0.01, 
                this.audioContext.currentTime + MuseRunnerGame.GAME_CONSTANTS.AUDIO_NOTE_DURATION
            );
            
            // Play the note
            oscillator.start(this.audioContext.currentTime);
            oscillator.stop(this.audioContext.currentTime + MuseRunnerGame.GAME_CONSTANTS.AUDIO_NOTE_DURATION);
            
        } catch (error) {
            console.log('Audio not available:', error);
        }
    }



    private switchToKey(key: GameKey): void {
        // End current platform and start new one
        this.endCurrentPlatform();
        this.startNewPlatform(key);
        this.playNote(key);
        
        // No jumping - character will smoothly move to platform height as it runs
        console.log(`Switching to ${key} platform - character will move to that height`);
        
        this.score += 10;
        this.scoreText.setText(`Score: ${Math.floor(this.score)}`);
    }

    private startNewPlatform(key: GameKey): void {
        const { height } = this.cameras.main;
        
        // Start platform ahead of character for proper landing
        this.platformStartX = this.character.x + MuseRunnerGame.GAME_CONSTANTS.PLATFORM_AHEAD_OFFSET;
        const platformY = height + MuseRunnerGame.GAME_CONSTANTS.LEVEL_HEIGHTS[key];
        const platformColor = MuseRunnerGame.GAME_CONSTANTS.LEVEL_COLORS[key];
        
        this.currentPlatform = this.add.rectangle(
            this.platformStartX + MuseRunnerGame.GAME_CONSTANTS.PLATFORM_CENTER_OFFSET, 
            platformY, 
            MuseRunnerGame.GAME_CONSTANTS.PLATFORM_WIDTH, 
            MuseRunnerGame.GAME_CONSTANTS.PLATFORM_HEIGHT, 
            platformColor
        );
        this.physics.add.existing(this.currentPlatform, true);
        this.platforms.add(this.currentPlatform);
        
        this.currentKey = key;
        console.log(`Started ${key} platform at height ${platformY}`);
    }

    private extendCurrentPlatform(): void {
        if (!this.currentPlatform) return;
        
        // Extend platform well ahead of character to ensure continuous running
        const newWidth = this.character.x - this.platformStartX + MuseRunnerGame.GAME_CONSTANTS.PLATFORM_EXTENSION;
        const newX = this.platformStartX + newWidth / 2;
        
        this.currentPlatform.width = Math.max(60, newWidth);
        this.currentPlatform.x = newX;
        
        // Update physics body properly
        const body = this.currentPlatform.body as Phaser.Physics.Arcade.StaticBody;
        body.setSize(this.currentPlatform.width, this.currentPlatform.height);
        body.updateFromGameObject();
    }

    private endCurrentPlatform(): void {
        // Current platform stays in place, just stop extending it
        this.currentPlatform = null;
        this.currentKey = null;
    }



    private getCurrentCharacterLevel(): number {
        if (!this.currentKey) return 0; // Neutral level
        return this.getKeyLevel(this.currentKey);
    }

    private getKeyLevel(key: GameKey): number {
        const levels: Record<string, number> = {
            'S': 0, 'D': 1, 'F': 2, 'G': 3, 'H': 4, 'J': 5, 'K': 6
        };
        return levels[key] ?? 0;
    }

    private getLevelKey(level: number): GameKey {
        const keys: GameKey[] = ['S', 'D', 'F', 'G', 'H', 'J', 'K'];
        return keys[Math.min(level, 6)] || 'S';
    }

    private isValidGameKey(key: string): key is GameKey {
        return ['S', 'D', 'F', 'G', 'H', 'J', 'K'].includes(key);
    }





    private gameOver(): void {
        this.isGameRunning = false;
        
        const { width, height } = this.cameras.main;
        
        // Stop camera follow
        this.cameras.main.stopFollow();
        
        // Game over screen
        const gameOverBg = this.add.rectangle(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2,
            width,
            height,
            0x000000,
            0.8
        );
        gameOverBg.setScrollFactor(0);
        
        this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 80,
            'Game Over!',
            {
                fontSize: '48px',
                color: '#e74c3c',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 20,
            `Final Score: ${Math.floor(this.score)}`,
            {
                fontSize: '24px',
                color: '#ffffff',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        const restartText = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 + 30,
            'Press R to restart or ESC for menu',
            {
                fontSize: '18px',
                color: '#bdc3c7',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        // Pulsing restart text
        this.tweens.add({
            targets: restartText,
            alpha: 0.5,
            duration: 1000,
            yoyo: true,
            repeat: -1
        });

        // Send score to parent (Reddit)
        if (window.parent) {
            window.parent.postMessage({
                type: 'GAME_OVER',
                data: { finalScore: Math.floor(this.score) }
            }, '*');
        }
    }
}