import { Scene } from 'phaser';

type GameKey = 'S' | 'D' | 'F' | 'G' | 'H' | 'J' | 'K';

export class PlatformManager {
    private platforms: Phaser.GameObjects.Group;
    private currentPlatform: Phaser.GameObjects.Rectangle | null = null;
    private currentKey: GameKey | null = null;
    private platformStartX: number = 0;

    private static readonly PLATFORM_CONSTANTS = {
        WIDTH: 120,
        HEIGHT: 20,
        EXTENSION: 150,
        AHEAD_OFFSET: 20,
        CENTER_OFFSET: 80,
        CLEANUP_DISTANCE: 400,
        LEVEL_HEIGHTS: {
            'S': -80,  'D': -120, 'F': -160, 'G': -200,
            'H': -240, 'J': -280, 'K': -320
        },
        LEVEL_COLORS: {
            'S': 0xe74c3c, 'D': 0xf39c12, 'F': 0xf1c40f, 'G': 0x2ecc71,
            'H': 0x3498db, 'J': 0x9b59b6, 'K': 0xe91e63
        }
    } as const;

    constructor(private scene: Scene) {
        this.platforms = this.scene.add.group();
    }

    initialize(): void {
        const { height } = this.scene.cameras.main;
        
        // Create neutral starting platform
        const neutralPlatform = this.scene.add.rectangle(200, height - 50, 800, 25, 0x95a5a6);
        this.scene.physics.add.existing(neutralPlatform, true);
        this.platforms.add(neutralPlatform);
    }

    startNewPlatform(key: GameKey, characterX: number): void {
        const { height } = this.scene.cameras.main;
        
        this.platformStartX = characterX + PlatformManager.PLATFORM_CONSTANTS.AHEAD_OFFSET;
        const platformY = height + PlatformManager.PLATFORM_CONSTANTS.LEVEL_HEIGHTS[key];
        const platformColor = PlatformManager.PLATFORM_CONSTANTS.LEVEL_COLORS[key];
        
        this.currentPlatform = this.scene.add.rectangle(
            this.platformStartX + PlatformManager.PLATFORM_CONSTANTS.CENTER_OFFSET, 
            platformY, 
            PlatformManager.PLATFORM_CONSTANTS.WIDTH, 
            PlatformManager.PLATFORM_CONSTANTS.HEIGHT, 
            platformColor
        );
        
        this.scene.physics.add.existing(this.currentPlatform, true);
        this.platforms.add(this.currentPlatform);
        this.currentKey = key;
    }

    extendCurrentPlatform(characterX: number): void {
        if (!this.currentPlatform) return;
        
        const newWidth = characterX - this.platformStartX + PlatformManager.PLATFORM_CONSTANTS.EXTENSION;
        const newX = this.platformStartX + newWidth / 2;
        
        this.currentPlatform.width = Math.max(60, newWidth);
        this.currentPlatform.x = newX;
        
        // Update physics body
        const body = this.currentPlatform.body as Phaser.Physics.Arcade.StaticBody;
        body.setSize(this.currentPlatform.width, this.currentPlatform.height);
        body.updateFromGameObject();
    }

    endCurrentPlatform(): void {
        this.currentPlatform = null;
        this.currentKey = null;
    }

    cleanupOldPlatforms(characterX: number): void {
        this.platforms.children.entries.forEach((platform) => {
            const rect = platform as Phaser.GameObjects.Rectangle;
            if (rect.x < characterX - PlatformManager.PLATFORM_CONSTANTS.CLEANUP_DISTANCE) {
                rect.destroy();
            }
        });
    }

    getPlatformsGroup(): Phaser.GameObjects.Group {
        return this.platforms;
    }

    getCurrentKey(): GameKey | null {
        return this.currentKey;
    }

    hasCurrentPlatform(): boolean {
        return this.currentPlatform !== null;
    }
}