import { Scene } from 'phaser';

type GameKey = 'S' | 'D' | 'F' | 'G' | 'H' | 'J' | 'K';

export interface InputState {
    pressedKeys: Set<string>;
    keyPressStartTime: Map<string, number>;
    lastPressedKey: GameKey | null;
}

export class InputManager {
    private gameKeys: { [key: string]: Phaser.Input.Keyboard.Key } = {};
    private inputState: InputState = {
        pressedKeys: new Set(),
        keyPressStartTime: new Map(),
        lastPressedKey: null
    };

    constructor(private scene: Scene) {}

    initialize(): void {
        // Setup keyboard keys
        this.gameKeys = {
            'S': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.S),
            'D': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.D),
            'F': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.F),
            'G': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.G),
            'H': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.H),
            'J': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.J),
            'K': this.scene.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.K)
        };
    }

    update(currentTime: number): { keyStateChanged: boolean; targetKey: GameKey | null } {
        let keyStateChanged = false;
        
        Object.keys(this.gameKeys).forEach(key => {
            const gameKey = this.gameKeys[key];
            if (!gameKey) return;
            
            // Key just pressed
            if (Phaser.Input.Keyboard.JustDown(gameKey)) {
                this.inputState.pressedKeys.add(key);
                this.inputState.keyPressStartTime.set(key, currentTime);
                this.inputState.lastPressedKey = key as GameKey;
                keyStateChanged = true;
            }
            
            // Key just released
            if (Phaser.Input.Keyboard.JustUp(gameKey)) {
                this.inputState.pressedKeys.delete(key);
                keyStateChanged = true;
            }
        });

        const targetKey = this.inputState.pressedKeys.size > 0 
            ? this.inputState.lastPressedKey 
            : null;

        return { keyStateChanged, targetKey };
    }

    getInputState(): InputState {
        return { ...this.inputState };
    }

    isValidGameKey(key: string): key is GameKey {
        return ['S', 'D', 'F', 'G', 'H', 'J', 'K'].includes(key);
    }
}